<h1 id="python-modules" tabindex="-1"><a class="header-anchor" href="#python-modules" aria-hidden="true">#</a> Python Modules</h1>
<p>In Python we have global functions like <code>print()</code> and <code>input()</code> that we can use directly in our own code, but many other useful functions in Python are not global, but have instead been put into modules. In this lecture we take a look at what a module is and how we can use the functions in them.</p>
